@import url(https://fonts.googleapis.com/css?family=Roboto:400,300);
* {
  margin: 0;
  padding: 0;
  box-sizing: border-box;
  list-style: none;
  text-decoration: none;
  font-family: Roboto, san-serif;
}

body {
  background-color: #f3f5f9;
}

label {
  display: inline-block;
  margin-bottom: 0.5rem;
}

input[type="text"],
select {
  display: block;
  padding: 0.5rem;
  width: 100%;
  background-color: white;
  border: 1px solid #e5e5e5;
  outline: none;
}

input[type="text"]:focus,
select:focus {
  border-color: rgb(239,126,173);
}

input[type="text"],
select {
  height: 34px;
}

input[type="checkbox"],
input[type="radio"] {
  cursor: pointer;
  position: relative;
  top: 5px;
  width: 22px;
  height: 22px;
  background-color: white;
  border: 1px solid #e5e5e5;
  outline: none;
  -moz-appearance: none;
  -webkit-appearance: none;
}

input[type="checkbox"]:checked {
  background-color: rgb(239, 126, 173);
  border: none;
}

input[type="checkbox"]:checked:after {
  display: block;
  content: "";
  height: 4px;
  width: 10px;
  border-bottom: 3px solid #fff;
  border-left: 3px solid #fff;
  transform: translate(5px, 6px) rotate(-45deg) scale(1);
}

.itemSection {
  width: 200px;
  height: 100%;
  background: #4b4276;
  padding: 30px 0px;
  position: fixed;
}

.itemSection h2 {
  color: #fff;
  text-transform: uppercase;
  text-align: center;
  margin-bottom: 30px;
}

.itemSection ul li {
  padding: 15px;
  border-bottom: 1px solid #bdb8d7;
  border-bottom: 1px solid rgba(0, 0, 0, 0.05);
  border-top: 1px solid rgba(255, 255, 255, 0.05);
  color: #bdb8d7;
  display: block;
  cursor: pointer;
}

.itemSection ul li.selected {
  background-color: #594f8d;
  color: #fff;
}

.itemSection ul li:hover {
  background-color: #594f8d;
  color: #fff;
}


.toggle{
    margin-right: 10px;
}


.button{
    padding: 10px;
    border: none;
    outline: none;
    width: 100px;
    cursor: pointer;
    color: #ffffff;
    background-color: #4b4276;
}
.button:disabled{
    background-color: grey;
}
.ActionButtons{
   margin: 0 10px ; 
}
.button{
    margin: 5px;
}
.itemHeader {
  padding: 10px;
  background: #fff;
  color: #717171;
  border-bottom: 1px solid #e0e4e8;
}

.buttonsContainer {
  display: flex;
  justify-content: flex-end;
}

.itemContent {
  margin-bottom: 20px;
}

.itemInnerContainer {
  width: 100%;
  margin-left: 200px;
}

.itemInnerContainer .itemInnerContent {
  margin: 20px;
  color: #717171;
  line-height: 25px;
}

.itemBody {
  display: flex;
  position: relative;
}

.itemBody .itemContainer {
  width: calc(100% - 200px);
}


